import vue from 'vue'
import Vuex from 'vuex'
vue.use(Vuex)


const  store  = new Vuex.Store({
    state:{
        currentPathName :'',
        user:{

        }
    },
    mutations:{
        setPath(state){
            state.currentPathName = localStorage.getItem("currentPathName")
            state.user = localStorage.getItem("user")
        },
    }
})

export default store
