import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Manage.vue'
import store from "@/store";
import Login from "@/views/Login";
import manage from "@/views/Manage";

Vue.use(VueRouter)

const routes = [
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import('../views/Login')
    },
    {
        path: '/register',
        name: 'Register',
        component: () => import('../views/Register')
    },
    {
        path: '/404',
        name: '404',
        component: () => import('../views/404')
    },

]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

//刷新页面会导致页面路由重置
export const setRoutes = () => {
    const storeMenus = localStorage.getItem("menus")
    if (storeMenus) {
        //拼装动态路由
        const manageRoute = {
            path: '/',
            name: 'Manage',
            component: () => import(/* webpackChunkName: "about" */ '../views/Manage.vue'),
            redirect: "/login",
            children: [
                { path: 'person', name: '个人信息', component: () => import('../views/Person.vue')},
                // { path: 'password', name: '修改密码', component: () => import('../views/Password.vue')}
            ]
        }
        const menus = JSON.parse(storeMenus)
        menus.forEach(item => {
            if (item.path) {
                let itemMenu = {
                    path: item.path.replace("/", ""),
                    name: item.name,
                    component: () => import(/* webpackChunkName: "about" */ '../views/' + item.pagePath + '.vue')
                }
                manageRoute.children.push(itemMenu)
            } else if (item.children) {
                item.children.forEach(item => {
                        if (item.path) {
                            let itemMenu = {
                                path: item.path.replace("/", ""),
                                name: item.name,
                                component: () => import(/* webpackChunkName: "about" */ '../views/' + item.pagePath + '.vue')
                            }
                            manageRoute.children.push(itemMenu)
                        }
                    }
                )
            }
        })
        //获取当前的路由对象名称数组
        const currentRouteName = router.getRoutes().map(v => v.name)
        if (!currentRouteName.includes('Manage')) {
            //动态添加到现在的路由对象里面
            router.addRoute(manageRoute)
        }
    }
}

//刷新页面重置路由
setRoutes()

//路由守卫
router.beforeEach((to, from, next) => {
        localStorage.setItem("currentPathName", to.name) //设置当前的路由名称，为了在header组件中使用
        store.commit("setPath")//出发store的数据更新

        //未找到路由的情况
        if (!to.matched.length){
            const storeMenus = localStorage.getItem("menus")
            if(storeMenus){
                next("/404")//放行路由
            }else {
                //路由跳回登录页面
                next("/login")
            }
        }
        //其他情况都放行
        next()
}
)
export default router
